import React from "react";
import {Grid,Row,Col} from "react-bootstrap";
import {Trainerinfo,TrainerName,TrainerCard,TrainerImage,TrainerTitle,TrainerSocial,TrainerCard1,TrainerDes,G} from "./styled"
import Asset from "../../utils/AssetService";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { fab } from '@fortawesome/free-brands-svg-icons'
import {faInstagram, faFacebookF} from '@fortawesome/fontawesome-free-brands';

const Trainers = () => {
    return(
        <React.Fragment>
            
    <TrainerTitle >
        <h2> Founders</h2>
    </TrainerTitle>
    <G>
        <Row>
            <Col md={2}>
                <TrainerCard >
                <TrainerImage>
                    <img src={Asset("assets/logos/satish.jpg")} width="200px" height="200px"/>
                    {/* <TrainerSocial>
                        <ul>
                            <li><div className="facebook"><span><FontAwesomeIcon icon={faFacebookF} color="white" size="lg"  /></span></div></li>
                            <li><div className="instagram"><span><FontAwesomeIcon icon={faInstagram}  color="white" size="lg"/></span></div></li>
                        </ul>
                        
                    </TrainerSocial> */}
                </TrainerImage>
                <Trainerinfo >
                    
                        <TrainerName ><h4>Satish Gaikwad</h4></TrainerName>
                        <TrainerDes><h3>Founder and Director</h3></TrainerDes>
                    </Trainerinfo>
                </TrainerCard>
                
        </Col>

        <Col md={4}>
            <div style={{paddingLeft:"15px",fontSize:"18px"}}> 
                Satish is the master par excellence who is a ICC coach and has a decade os experience, having worked with over 15,000 players including international, national and ranji trophy players. He has worked for well-known academies around the world which includes academies in UK, Dubai and Sri-Lanka. He has a deep understanding of bio-mechanics as well.
            </div>
        </Col>
        
        
        
            <Col md={2}>
                <TrainerCard1 >
                <TrainerImage>
                    <img src={Asset("assets/logos/swati.jpg")} width="200px" height="200px"/>
                    {/* <TrainerSocial>
                        <ul>
                            <li><div className="facebook"><span><FontAwesomeIcon icon={faFacebookF} color="white" size="lg"  /></span></div></li>
                            <li><div className="instagram"><span><FontAwesomeIcon icon={faInstagram}  color="white" size="lg"/></span></div></li>
                        </ul>
                        
                    </TrainerSocial> */}
                </TrainerImage>
                <Trainerinfo >
                    
                        <TrainerName ><h4 style={{paddingLeft:"50px"}}>Swati Saraf</h4></TrainerName>
                        <TrainerDes><h3 style={{fontSize:"18px",paddingLeft:"15px"}}>Co-Founder and Director</h3></TrainerDes>
                    </Trainerinfo>
                </TrainerCard1>
                
        </Col>

        <Col md={4}>
            <div style={{paddingLeft:"95px",fontSize:"18px"}}>
                Swati is a megamind with tons of experience and insights into the day-to-day operations and an unparalleled experience of 12 years. She has an eye for detail and has the most effective, economic and empact heavy solutions for our clients.
            </div>
        </Col>
        
        </Row>
    </G>
    
    </React.Fragment>
)

    
}

export default Trainers;