import React from "react";
import Asset from "../../utils/AssetService";
import {Grid,Row,Col} from "react-bootstrap";
import {ServicesTitle,Cards,ServicesBlock} from "./styled"

class Services extends React.Component {
    render (){
        return (
            <React.Fragment>
                <ServicesBlock>
                    <ServicesTitle >
                        <h2> Here's What We Do</h2>
                    </ServicesTitle>
                    <Cards>
                        <Row>
                            <Col md={4}>
                                <img src={Asset("assets/logos/vision1.jpg")} width="380px" height="200px"/>
                            </Col>
                            <Col md={8}>
                                <h3 style={{textAlign:"left"}}>High Performance Coaching</h3>
                                <div style={{width:"600px",fontSize:"18px",textAlign:"left"}}>High performance sport coaching is a complex collection of inter-disciplinary practices with a focus on the preparation and competition requirements of athletes and teams engaged at any level of sporting competition.</div>
                            </Col>
                        </Row>
                        <Row>
                            
                            <Col md={4}>
                                <div style={{paddingLeft:"0px",paddingTop:"35px"}}>
                                    <h3 style={{textAlign:"left"}}>Scientific Assessment</h3>
                                    <div style={{width:"600px",fontSize:"18px",textAlign:"left"}}>The regular monitoring via scientific assessment of physical fitness and sports centric performance is important in any sport to increase the likelihood of success. Sport specific assessment process leads to improved results.</div>
                                </div>
                            </Col>
                            <Col md={8}>
                                <div style={{paddingTop:"40px",paddingRight:"0px"}}>
                                    <img src={Asset("assets/logos/bowling.jpg")} width="380px" height="200px"/>
                                </div>
                            </Col>
                        </Row>
                        <Row>
                            <Col md={4}>
                            <div style={{paddingTop:"40px"}}>
                                <img src={Asset("assets/logos/strength.jpg")} width="380px" height="200px"/>
                            </div>
                            </Col>
                            <Col md={8}>
                            <div style={{paddingTop:"40px"}}>
                                <h3 style={{textAlign:"left"}}>Strength &amp; Conditioning</h3>
                                <div style={{width:"600px",fontSize:"18px",textAlign:"left"}}>Strength &amp; Conditioning is physical and physiological development of athletes for better performance and it encompasses the entire develeopment of athlete and what is needed to improve physical performance.</div>
                            </div>
                            </Col>
                        </Row>
                        <Row>
                            
                        <Col md={4}>
                        <div style={{paddingLeft:"0px",paddingTop:"35px"}}>
                            <h3 style={{textAlign:"left"}}>Sports Nutrition &amp; Pychology</h3>
                            <div style={{width:"600px",fontSize:"18px",textAlign:"left"}}>Nutrition is important for athletes because it provides the source of energy. The food we eat has an impact on our strength, training, performance and recovery. Psychology is about improving our attitude and mental game skills to help us perform better by identifying limitations and embracing a healthier philosophy about sports.</div>
                        </div>
                    </Col>
                    <Col md={8}>
                        <div style={{paddingTop:"40px",paddingRight:"0px"}}>
                            <img src={Asset("assets/logos/nutrition.jpg")} width="380px" height="200px"/>
                        </div>
                    </Col>
                        </Row>
                    </Cards>
                    </ServicesBlock>
            </React.Fragment>
        )
    }
    
}

export default Services;