import styled from "styled-components";
import Asset from "../../utils/AssetService";
import {Grid} from "react-bootstrap";

const G = styled(Grid)`
margin-right:300px !important;
`;

const TrainerTitle = styled.div`
text-align:center;
margin-bottom:50px;
margin-top:60px;
text-transform:uppercase;
`;

const TrainerCard = styled.div`
width:200px;
height:200px;
`;

const TrainerCard1 = styled.div`
width:200px;
height:200px;
padding-left:75px;

`;

const Trainerinfo = styled.div`
color:white;
background-color:black;
height:80px;
width:200px;
:hover {
    background-color:#1cf1f1;
}
`;

const TrainerName = styled.div`

display:inline-block;
h4 {
    padding-left:40px;
    font-size:20px;
}

`;

const TrainerDes = styled.div`
margin-top:-22px;
display:inline-block;
h3 {
    padding-left:25px;
    font-size:18px;
}
`;

const TrainerImage = styled.div`
border:solid 1px lightgrey;
`;

const TrainerSocial = styled.div`
width:50px;
height:90px;
position:absolute;
background-color: rgba(2, 2, 2, 0.65);
top:50px;
div {
    border: 0.5px solid white;
    width:30px;
    height:30px;
    text-align:center;
    vertical-align:center;
}
ul {
    margin:0;
    padding:0;
}
li {
    list-style-type:none;
    padding-top:10px;
    padding-left:5px;
}
span {
    padding-top: 3px;
    display: inline-block;
}
.facebook {
    :hover{
        background-color:#4267b2;
    }
}
.instagram {
    :hover{
        background-color:#e4405f;
    }
}

`;


export {Trainerinfo,TrainerName,TrainerCard,TrainerImage,TrainerTitle,TrainerSocial,TrainerCard1,TrainerDes,G}