import React from "react";
import Asset from "../../utils/AssetService";
import {Grid,Row,Col} from "react-bootstrap";
import {VisionTitle} from "./styled";
const Vision = () => {
    return(
        <React.Fragment>
        
        <VisionTitle>
            <h2>Our Vision</h2>
        </VisionTitle>

        <div style={{paddingTop:"30px"}}>
            <Row>
                <Col md={6}>
                    <div style={{height:"360px",width:"500px",backgroundImage: `${`url(${Asset("assets/logos/vision2.jpg")})`}`,backgroundRepeat: "no-repeat",
                        backgroundPosition: "center center",backgroundSize: "cover",marginLeft:"180px"}}>
                    </div>
                </Col>
                <Col md={1}>
                    <div style={{display:"flex",flexDirection: "column",width:"500px"}}>
                        <div><img src={Asset("assets/icons/olympia.svg")} width="60px" height="70px"/></div>
                        <div  style={{paddingTop: "40px"}}><img src={Asset("assets/icons/women-stressed.svg")} width="60px" height="60px"/></div>
                        <div  style={{paddingTop: "40px"}}><img src={Asset("assets/icons/brain.svg")} width="60px" height="60px"/></div>
                        <div  style={{paddingTop: "30px"}}><img src={Asset("assets/icons/podium.svg")} width="60px" height="60px"/></div>
                    </div>
                </Col>
                <Col md={5}>
                    <div style={{display:"flex",flexDirection: "column",width:"500px"}}>
                        <div style={{fontSize:"18px"}}>Identify, develop talent from all parts of the country and to help them excel at the international stage under our Long Term Athlete Development Programme</div>
                        <div  style={{paddingTop: "30px",fontSize:"18px"}}>Create awareness about and prevent burnout in Sport. A high percentage of young athletes suffer mental or physical burnout and opt of sport despite having the talent to excel. </div>
                        <div  style={{paddingTop: "30px",fontSize:"18px"}}>Facilitate a career in Sports by focusing on Athlete Career and Education which would include their life post-retirement. </div>
                        <div  style={{paddingTop: "40px",fontSize:"18px"}}>Create a culture of sporting excellence on which the society can thrive upon. </div>
                    </div>
                </Col>
            </Row>
        </div>

    </React.Fragment>
)
}

export default Vision;